import api from "../../api/index.js"
const {reqVideoUrl} = api
export default {
	data() {
		return {
			vID:'',
			isAddRecommend: true,
			userInfo:''
		}
	},
	computed:{
		isAdmin(){
			return this.userInfo.openId === 'oG4nU5N33zP1Q0DeCUSz88mjhsdQ'
		}
	},
	onLoad() {
		this.userInfo = getApp().globalData.userInfo
	},
	onReady() {
		console.log(this.userInfo);
	},
	methods: {
		change(){
			this.isAddRecommend = !this.isAddRecommend
		},
		update(name){
			wx.cloud.callFunction({ // 
			  // 要调用的云函数名称
			  name,
			  success: res => { 
				  console.log(res);
				  uni.showModal({
				  	title: '更新成功' 
				  })
			  },
			  fail: err => {
				uni.showModal({
					title:'更新失败',
					showCancel:false
				})
			  }
			})	
		},
		add(){
			if(this.vID === ''){
				uni.showToast({
					title:'请输入vID'
				})
				return
			}
			let length = 0
			reqVideoUrl(this.vID).then(res => { // 判断一下 是否已经添加
				if(res.length) {
					uni.showToast({
						title:'此番已经添加了哦'
					})
					return 	
				} 
				wx.cloud.callFunction({ // 调用云函数读取用户信息
				  // 要调用的云函数名称
				  name: 'addOneVideo',
				  data:{
					  vID: this.vID,
					  isAddRecommend: this.isAddRecommend
				  },
				  success: res => { 
					  uni.showModal({
					  	title: '更新成功' ,
						showCancel:false
					  })
				  },
				  fail: err => {
					  console.log(err);
					uni.showModal({
						title:'更新失败' ,
						showCancel:false
					})
				  }
				})	
			})
			
			
		}
	}
}
